var React = require('react');
var ReactDOM = require('react-dom');

var GreeterMessage = React.createClass({
  render: function(){
    var name = this.props.name
    var msg = this.props.msg
    return (
      <div>
        <h1>Hello {name}!</h1>
        <p>{msg}</p>
      </div>
    )
  }
})

var GreeterForm = React.createClass({
  
  onFormSubmit: function(e){
    e.preventDefault()
    var updates = {}
    var name = this.refs.name.value
    var msg = this.refs.msg.value
    
    if(name.length > 0){
      updates.name = name
      this.props.onNewData(updates)
      this.refs.name.value = ''
    }
    
    if(msg.length > 0){
      updates.message = msg
      this.props.onNewData(updates)
      this.refs.msg.value = ''
    }
  },
  render: function(){
    return (
      <div>
        <form onSubmit={this.onFormSubmit}>
          <div>
            <input type='text' ref='name' placeholder='enter name here!' />
          </div>
          <div>
            <textarea type='text' ref='msg' placeholder='enter message here!' />
          </div>
          <button>Submit</button>
        </form>
      </div>
    )
  }
})

var Greeter = React.createClass({
  
  getDefaultProps: function(){
    return {
      name: 'React',
      message: 'This is from a component!'
    }
  },
  
  getInitialState: function(){
    return {
      name: this.props.name,
      message: this.props.message
    }
  },
  
  handleNewData: function(update){
    this.setState(update)
  },
  
  render: function(){
    var name = this.state.name
    var msg = this.state.message
    return(
      <div>
        <GreeterMessage name={name} msg={msg}/>
        <GreeterForm onNewData={this.handleNewData} />
      </div>
    )
  }
})

var firstName = 'Daviz'

ReactDOM.render(
  <Greeter name={firstName} message='Default message!' />,
  document.getElementById('app')
)



